































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { Form as ElForm } from 'element-ui';
import {
	loginWithPassword,
	loginStudentWithPassword,
	loginWithAuthCode,
	postCodeToCheck,
	studentLoginWithAuthCode,
	studentCheckCode,
} from '@/http/api';
import { RuleForm, Res } from '@/interface';
import { setWeekDay } from '../utils/utils';

@Component
export default class LoginSelect extends Vue {
	@Prop({
		required: true,
		validator(value) {
			return ['teacher', 'student'].indexOf(value) !== -1;
		},
	}) private loginAuth: string = 'teacher';
	get auth() {
		return this.loginAuth;
	}
	private code: string = '';
	private ruleForm: RuleForm = {
		userName: '',
		password: '',
		authCode: '',
	};
	private rules: object = {
		userName: [
			{ validator: this.validateUserName, trigger: 'blur' },
		],
		password: [
			{ validator: this.validatePassword, trigger: 'blur' },
		],
		authCode: [
			{ validator: this.validateAuthCode, trigger: 'blur' },
		],
	};
	private isPhone: boolean = false;
	private activeName: string = 'teacher';
	private submitForm(formName: string) {
		(this.$refs[formName] as any).validate((valid: any) => {
			if (valid) {
				this.login();
			} else {
				return false;
			}
		});
	}
	// 校验手机号码或学号
	private validateUserName(rule: any, value: string, callback: any): void {
		const pattern = this.auth === 'teacher' || this.isPhone ? /^1[3-9]\d{9}$/ : /^\d{8}$/;
		const message: string = this.auth === 'teacher' || this.isPhone ? '手机号码' : '学号';
		if (!pattern.test(value)) {
			callback(new Error('请输入正确格式的' + message));
		}
		callback();
	}
	// 校验密码
	private validatePassword(rule: any, value: string, callback: any): void {
		if (value.length < 6 || value.length > 20) {
			callback(new Error('格式错误，请输入长度为6~20位密码'));
		}
		callback();
	}
	// 校验验证码
	private validateAuthCode(rule: any, value: string, callback: any): void {
		if (!(/^\d{6}$/.test(value))) {
			callback(new Error('请输入6为数字验证码'));
		}
		callback();
	}
	// 切换密码登录和手机验证码登录
	private change(): void {
		this.isPhone = !this.isPhone;
	}

	private async login() {
		let res: Res = {
			code: '',
			data: [],
			msg: '',
		};
		if (this.isPhone) {
			const params = {
				phone: this.ruleForm.userName,
				code: this.ruleForm.authCode,
			};
			res = (this.auth === 'teacher' ? await postCodeToCheck(params) : await studentCheckCode(params));
		}
		if (this.auth === 'teacher' && !this.isPhone) {
			res = await loginWithPassword({
				username: this.ruleForm.userName,
				password: this.ruleForm.password,
			});
		}
		if (this.auth === 'student' && !this.isPhone) {
			res = await loginStudentWithPassword({
				uId: this.ruleForm.userName,
				password: this.ruleForm.password,
			});
		}
		console.log('res: ', res);
		if (parseInt(res.code, 10) === 1) {
			localStorage.setItem('as_token', res.data[0].token);
			if (res.data.length === 2) {
				sessionStorage.setItem('uId', res.data[1].uId);
				sessionStorage.setItem('name', res.data[1].username);
			}
			localStorage.setItem('auth', this.auth);
			this.$router.replace(`/${this.auth}`);
		} else {
			this.$message.error(res.msg);
		}
	}
	private async loginWithCode() {
		let res: Res = {
			data: [],
			code: '',
			msg: '',
		};
		const params = {
			phone: this.ruleForm.userName,
		};
		if (this.auth === 'teacher') {
			res = await loginWithAuthCode(params);
		}
		if (this.auth === 'student') {
			res = await studentLoginWithAuthCode(params);
		}
		parseInt(res.code, 10) === 1 ? this.$message.success(res.msg) : this.$message.error(res.msg);
	}
}
